import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';

// material components
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatToolbarModule, MatDialogModule, MatSelectModule, MatExpansionModule, MatCheckboxModule, MatRadioModule, MatTabsModule, MatTooltipModule, MatSlideToggleModule, MatGridListModule, MatProgressSpinnerModule, MatSidenavModule, MatIconModule, MatProgressBarModule, MatPaginatorModule, MatButtonModule, MatListModule, MatCardModule, MatFormFieldModule, MatInputModule } from '@angular/material';
import { MatTableModule } from '@angular/material/table';
import { CdkTableModule } from '@angular/cdk/table';
import { MatSnackBarModule } from '@angular/material/snack-bar';

// router
import { AppRouterModule } from './app-router/app-router.module';
import { HashLocationStrategy, LocationStrategy} from '@angular/common';

//services
import { ConnectionService } from './services/connection.service';
import { InterceptorService } from './services/interceptor.service';
import { DialogService } from './common/notification/dialog/dialog.service';
import { AgmCoreModule } from '@agm/core';
import { CookieService } from 'ngx-cookie-service';

// app sections
import { HomeComponent } from './views/home/home.component';
import { DialogComponent } from './common/notification/dialog/dialog.component';
import { DynamicFormComponent } from './common/forms/dynamic-form/dynamic-form.component';
import { CustomersComponent } from './views/customers/customers.component';
import { FlightsComponent } from './views/flights/flights.component';
import { OrdersComponent } from './views/orders/orders.component';
import { ShipmentsComponent } from './views/shipments/shipments.component';
import { SidebarComponent } from './views/sidebar/sidebar.component';
import { ShipmentDetailComponent } from './views/shipments/shipment-detail/shipment-detail.component';
import { LoginComponent } from './views/login/login.component';
import { FlightDetailComponent } from './views/flights/flight-detail/flight-detail.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    DialogComponent,
    DynamicFormComponent,
    CustomersComponent,
    FlightsComponent,
    OrdersComponent,
    ShipmentsComponent,
    SidebarComponent,
    ShipmentDetailComponent,
    LoginComponent,
    FlightDetailComponent
  ],
  imports: [
    BrowserModule,
    AppRouterModule,
    FormsModule,
    HttpModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MatToolbarModule,
    MatSidenavModule,
    MatIconModule,
    MatProgressBarModule,
    MatPaginatorModule,
    MatButtonModule,
    MatListModule,
    MatCardModule,
    MatTabsModule,
    MatSnackBarModule,
    MatFormFieldModule,
    MatInputModule,
    MatGridListModule,
    MatProgressSpinnerModule,
    MatTableModule,
    CdkTableModule,
    ReactiveFormsModule,
    MatSlideToggleModule,
    MatSelectModule,
    MatExpansionModule,
    MatCheckboxModule,
    MatRadioModule,
    MatTooltipModule,
    MatDialogModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyAnsr-uSrhCSfnLXOrADz9cv9ATXiLazKU',
      apiVersion: "4"
    })
  ],
  providers: [ConnectionService,
    CookieService,
    DialogService,
    { provide: HTTP_INTERCEPTORS, useClass: InterceptorService, multi: true },
    { provide: LocationStrategy, useClass: HashLocationStrategy}
  ],
  bootstrap: [AppComponent],
  entryComponents: [DialogComponent]
})
export class AppModule { }



