import { Component, OnInit } from '@angular/core';
import { ConnectionService } from '../../services/connection.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MatTableDataSource } from '@angular/material';
import { AppComponent } from '../../app.component';

declare var window: any;

@Component({
  selector: 'app-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.scss']
})
export class CustomersComponent implements OnInit {

  customers: any = [];
  customer_shipments: any = [];
  panelOpenState: boolean;


  displayedColumns = ['id', , 'name', 'phone','email', 'wechat','region'];
  dataSource = new MatTableDataSource();

  constructor(
    private connection: ConnectionService,
    private ac: AppComponent,
		private router: Router,
  ) { }

  ngOnInit() {
    this.getCustomers();
  }

  getCustomers() {
    let url = window.otto_data + '/dev.php/api/beta/customers';

    this.connection.get(url).subscribe(res => {
      let data = [];
      this.customers = res.data;

      for (let i = 0; i < res.data.length; i++) {
        const customer = res.data[i];
        data.push(customer);
      }
      // console.log(this.datasource);
      console.log(data);
      this.dataSource.data = data;

    })

  }

  navigate(id) {
    // let hash = this.router.url.replace(/^\/admin\/([a-z0-9]*)\/(.*)/g, '$1');
    // console.log(id);
    this.router.navigateByUrl('/shipments/' + id);
  }

}



