import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { LoginComponent } from '../views/login/login.component';
import { HomeComponent } from '../views/home/home.component';
import { CustomersComponent } from '../views/customers/customers.component';
import { FlightsComponent } from '../views/flights/flights.component';
import { FlightDetailComponent } from '../views/flights/flight-detail/flight-detail.component';
import { OrdersComponent } from '../views/orders/orders.component';
import { ShipmentsComponent } from '../views/shipments/shipments.component';
import { ShipmentDetailComponent } from '../views/shipments/shipment-detail/shipment-detail.component';

// import { SidebarComponent } from '../views/sidebar/sidebar.component';


// let's set the main routes
const routes: Routes = [
    // this is the main screen after the app has been installed
    { path: '', component: HomeComponent },
    { path: 'login', component: LoginComponent },
    { path: 'customers', component: CustomersComponent },
    { path: 'flights', component: FlightsComponent },
    { path: 'flights/:id', component: FlightDetailComponent },
    { path: 'orders', component: OrdersComponent },
    { path: 'shipments', component: ShipmentsComponent },
    { path: 'shipments/:id', component: ShipmentDetailComponent },

    // { path: 'sidebar', component: SidebarComponent },

    // fallback to a specific route
    { path: '**', redirectTo: ''}
];

@NgModule({
    imports: [ RouterModule.forRoot(routes) ],
    exports: [ RouterModule ]
})
export class AppRouterModule { }



