import { Component, OnInit } from '@angular/core';
import { ConnectionService } from '../../services/connection.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MatTableDataSource } from '@angular/material';
import { AppComponent } from '../../app.component';

declare var window: any;

@Component({
  selector: 'app-shipments',
  templateUrl: './shipments.component.html',
  styleUrls: ['./shipments.component.css']
})
export class ShipmentsComponent implements OnInit {

  templates: any = [];
  shipments: any = [];
	total: number = 1;
  per_page: number = 10;
  page_size: any = [5,10,25,50];
  pageIndex: number = 0;
  filter_search: string = '';

  link: any;
  id: any;

  displayedColumns = ['id', 'receiver_name', 'receiver_address', 'price', 'reference'];
  dataSource = new MatTableDataSource();

  constructor(
    private connection: ConnectionService,
    private ac: AppComponent,
    private router: Router,
  ) { }

  ngOnInit() {
    this.getShipments();
  }

  getShipments() {
    let url = window.otto_data + '/dev.php/api/beta/shipments';

    this.connection.get(url).subscribe(shipments => {
      for (let i = 0; i < shipments.data.length; i++) {
        const shipment = shipments.data[i];
        this.shipments.push(shipment);
      }
      this.total = this.shipments.length;
      const start = this.pageIndex * this.per_page;
      this.dataSource.data = this.shipments.slice(start, (start + this.per_page));
    })
  }

  switchPage($event) {
        const list = this.shipments;
        const start = $event.pageIndex * $event.pageSize;
        this.per_page = $event.pageSize;
        this.pageIndex = this.pageIndex;
        this.dataSource.data = list.slice(start, (start + $event.pageSize));
  }

  navigate(row) {
    this.id = row.shipment_id;
    this.router.navigateByUrl('/shipments/' + this.id);
	}

}



