import { Component, OnInit } from '@angular/core';
import { ConnectionService } from '../../../services/connection.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MatTableDataSource } from '@angular/material';
import { AppComponent } from '../../../app.component';

import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Headers, RequestOptions, URLSearchParams, Http, RequestMethod, Request } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/map'

declare var window: any;

@Component({
  selector: 'app-flight-detail',
  templateUrl: './flight-detail.component.html',
  styleUrls: ['./flight-detail.component.scss']
})
export class FlightDetailComponent implements OnInit {

  flight: any;
  customer: any;
  id: any;

  d_lat: number;
  d_lng: number;
  a_lat: number;
  a_lng: number;

  constructor(
    private connection: ConnectionService,
    private ac: AppComponent,
    private router: Router,
    private ar: ActivatedRoute,

    private client: HttpClient,
    private http: Http,
  ) { }

  ngOnInit() {
    this.ar.params.subscribe(params => {
      this.id = params.id;
    })
    console.log(this.id);
    this.getFlight(this.id);
    this.d_lat = 56.1304;
    this.d_lng = -106.3468;
    this.a_lat = 39.913818;
    this.a_lng = 116.363625;
  }

  getFlight(id) {
    this.connection.get(window.otto_data + '/dev.php/api/beta/flights/' + id).subscribe(res => {
      console.log(res);
      this.flight = res.data;
      // this.s_lat = this.shipment.s_latitude;
      // this.s_lng = this.shipment.s_longitude;
      // this.r_lat = this.shipment.r_latitude;
      // this.r_lng = this.shipment.r_longitude;

      this.connection.get(window.otto_data + '/dev.php/api/beta/customers/' + this.flight.user_id).subscribe(res => {
        this.customer = res.data;
        console.log(this.customer);
      })
    })
  }

}



