import { Component, OnInit } from '@angular/core';
import { ConnectionService } from '../../services/connection.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MatTableDataSource } from '@angular/material';
import { AppComponent } from '../../app.component';

declare var window: any;

@Component({
  selector: 'app-flights',
  templateUrl: './flights.component.html',
  styleUrls: ['./flights.component.css']
})
export class FlightsComponent implements OnInit {

  flights: any = [];
  id: any;

  displayedColumns = ['id', 'name', 'date_departure', 'date_arrival', 'price', 'reference'];
  dataSource = new MatTableDataSource();

  constructor(
    private connection: ConnectionService,
    private ac: AppComponent,
    private router: Router,
  ) { }

  ngOnInit() {
    this.getFlights();
  }

  getFlights() {
    let url = window.otto_data + '/dev.php/api/beta/flights';

    this.connection.get(url).subscribe(res => {
      let data = [];

      for (let i = 0; i < res.data.length; i++) {
        const flight = res.data[i];
        data.push(flight);
      }
      // console.log(this.datasource);
      console.log(data);
      this.dataSource.data = data;
    })
  }

  navigate(row) {
    this.id = row.flight_id;
    this.router.navigateByUrl('/flights/' + this.id);
	}

}



