import { Component, OnInit } from '@angular/core';
import { ConnectionService } from '../../../services/connection.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MatTableDataSource } from '@angular/material';
import { AppComponent } from '../../../app.component';

import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Headers, RequestOptions, URLSearchParams, Http, RequestMethod, Request } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/map'

declare var window: any;

@Component({
  selector: 'app-shipment-detail',
  templateUrl: './shipment-detail.component.html',
  styleUrls: ['./shipment-detail.component.scss']
})
export class ShipmentDetailComponent implements OnInit {

  shipment: any;
  customer: any;
  items: any;
  total_price: any = 0;
  id: any;

  s_lat: number;
  s_lng: number;
  r_lat: number;
  r_lng: number;

  constructor(
    private connection: ConnectionService,
    private ac: AppComponent,
    private router: Router,
    private ar: ActivatedRoute,

    private client: HttpClient,
    private http: Http,
  ) { }

  ngOnInit() {
    this.ar.params.subscribe(params => {
      this.id = params.id;
    })
    console.log(this.id);
    this.getShipment(this.id);
  }

  getShipment(id) {
    this.connection.get(window.otto_data + '/dev.php/api/beta/shipments/' + id).subscribe(res => {
      console.log(res);
      this.shipment = res.data;
      this.s_lat = this.shipment.s_latitude;
      this.s_lng = this.shipment.s_longitude;
      this.r_lat = this.shipment.r_latitude;
      this.r_lng = this.shipment.r_longitude;

      this.connection.get(window.otto_data + '/dev.php/api/beta/customers/' + this.shipment.user_id).subscribe(res => {
        this.customer = res.data;
        console.log(this.customer);
      })

      this.connection.get(window.otto_data + '/dev.php/api/beta/shipments/' + id + '/items').subscribe(res => {
        this.items = res.data;
        for (let item of res.data) {
          this.total_price += parseFloat(item.total_price);
        }
        this.total_price = this.total_price.toFixed(2);
        console.log(this.items);
      })
    })
  }

}



